























































































import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class UploadBook extends Vue {
  @Prop() private searchObj: any;
  @Prop() private bookList: any;
  @Prop() public getBookList: any;
  @Prop() public loading: any;

  public handleSizeChange(val: number) {
    this.searchObj.size = val;
    this.getBookList();

  }

  public handleCurrentChange(val: number) {
    this.searchObj.current = val;
    this.getBookList();
  }

}
