






















import { HomeService } from '@/service';
import { ElForm } from '@/types/element-ui';
import { Component, Vue } from 'vue-property-decorator';
import List from './list.vue';

export interface search {
  bookName: string;
  bookisbn: string;
  current: number;
  size: number;
}

@Component({ components: { List } })
export default class UploadBook extends Vue {
  public searchObj: search = {
    bookName: '',
    bookisbn: '',
    current: 1,
    size: 10,
  };
  public bookList: any[] = [];
  public loading: boolean = false;

  public created() {
    this.getBookList();
  }

  public getBookList() {
    this.loading = true;
    HomeService.getBookList(this.searchObj).then((res: any) => {
      this.loading = false;
      this.bookList = res.data.records;
    });
  }

  public onSubmit() {
    this.getBookList();
  }

  public resetForm(formName: string | number) {
    (this.$refs[formName] as ElForm).resetFields();
  }
}
